@use "global" as *;

 .blog-cards {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    column-gap: rem(40);
    row-gap: rem(40);
    @include mq("md") {
        grid-template-columns: repeat(3, 1fr);
    }
 }

 .blog-cards--sub {
    // display: grid;
    // grid-template-columns: repeat(1, 1fr);
    // column-gap: rem(40);
    // row-gap: rem(40);
    @include mq("md") {
        grid-template-columns: repeat(2, 1fr);
        row-gap: rem(80);
    }
 }